import React from 'react';
import { withRouter } from 'react-router-dom';

class Tab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    
    render() {
        const { pathname } = this.props.location;
        const btn = ['店铺', '客服', '收藏', '加入购物车', '立即购买']
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
            {btn.map((item, idx) => <button key={idx} style={{ marginLeft: '10px', marginTop: '5px'}}>{`${item}`}</button>)}
              
                
            </div>
        );
    }
}
export default withRouter(Tab);