import React from 'react'
import Tab from './Tab'
import { Link } from 'react-router-dom';
const Detail = () => {
    return (
        <div>
            <img src={'https://dss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2925267655,683723028&fm=26&gp=0.jpg'} style={{width:'100%',position:'absolute'}} />
            {/* absolute是相对于浏览器定位 */}
            <Link to='/react3/build'>
                <button
                    // onClick={() => {window.location.href='/'}}
                    style={{
                        width:'30px',
                        height:'30px',
                        background:'rgba(0, 0, 0, 0.4)',
                        color:'#fff',
                        fontSize:'20px',
                        borderRadius:'100%',
                        position:'relative',    //relative是相对于前面容器定位
                        margin:'15px'
                    }}
                ><span>&lt;</span></button>
            </Link>
            <Tab />
        </div> 
    )
}
export default Detail;